import axios from '@/utils/uniAxios.js'
export default {
	data() {
		return {
			username: '',
			leftIcon: '../../static/delivery/phone.png',
			code: '发送验证码',
			disabled: false,
			num: 60,
			phone: '',
			authCode: ''
		}
	},
	mounted() {

	},
	methods: {
		login() {
			var _this = this;
			uni.request({
				method: 'POST',
				url: 'http://39.98.122.62:8085/sso/user/login',
				data: {
					phone: _this.phone,
					authCode: _this.authCode
				},
				header: {
					'content-type': 'application/json'
				},
				success(res) {
					if(res.data.code==200){
						console.log(res.data.data)
						var token = res.data.data.token;
						uni.setStorage('gt',token);
						uni.switchTab({ 
							url:'../user/user?phone='+_this.phone,
							success:(res)=> { 
								 let page = getCurrentPages().pop();  //跳转页面成功之后
								 if (!page) return;   
								 page.loadData(); //如果页面存在，则重新刷新页面
							  }
						});
					}
					
				}
			})

		},
		// 发送验证码
		postCode() {
			if (this.phone == '') {
				this.$api.msg('请输入手机号码')
			} else {
				var _this = this;
				uni.request({
					method: 'POST',
					url: 'http://39.98.122.62:8085/sso/user/sendCode',
					data: {
						telephone: _this.phone
					},
					header: {
						'content-type': 'application/json'
					},
					success(res) {
						_this.$api.msg('发送成功')
						if(res.data.code==200){
							_this.disabled = true;
							_this.code = _this.num + 's';
							var timer=setInterval(() => {
								_this.num--;
								_this.code = _this.num + 's';
								if (_this.num == 0) {
									clearInterval(timer)
									_this.code = '重新发送';
									_this.num = 60;
								}
							}, 1000)
						}
						
					}
				})
			}

		}

	}
}
